@font-face {
  font-family: 'Arial';
  src: url("../fonts/ArialBold.woff") format("woff"), url("../fonts/ArialBold.woff2") format("woff2"), url("../fonts/ArialBold.ttf") format("truetype");
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: 'Arial';
  src: url("../fonts/ArialRegular.woff") format("woff"), url("../fonts/ArialRegular.woff2") format("woff2"), url("../fonts/ArialRegular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace;
  font-size: 1em; }

a {
  background-color: transparent;
  cursor: pointer; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  border: none;
  padding: 0; }

button {
  cursor: pointer; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

li {
  list-style-type: none; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

@font-face {
  font-family: 'Arial';
  src: url("../fonts/ArialBold.woff") format("woff"), url("../fonts/ArialBold.woff2") format("woff2"), url("../fonts/ArialBold.ttf") format("truetype");
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: 'Arial';
  src: url("../fonts/ArialRegular.woff") format("woff"), url("../fonts/ArialRegular.woff2") format("woff2"), url("../fonts/ArialRegular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap; }

.f-basic {
  font-size: 16px;
  color: #232323;
  line-height: 24px;
  font-weight: 400; }
  @media (min-width: 1020px) {
    .f-basic {
      font-size: 16px;
      color: #232323;
      line-height: 24px; } }

.f-basicText {
  font-size: 12px;
  color: #232323;
  line-height: 18px;
  font-weight: 400; }
  @media (min-width: 1020px) {
    .f-basicText {
      font-size: 16px;
      color: #232323;
      line-height: 24px; } }

.f-basicTitle {
  font-weight: 700;
  font-size: 24px;
  color: #5164ac;
  line-height: 28px; }
  @media (min-width: 1020px) {
    .f-basicTitle {
      font-size: 32px;
      color: #5164ac;
      line-height: 38px; } }

.f-basicTitle2 {
  font-size: 14px;
  color: #232323;
  line-height: 20px;
  font-weight: 400; }
  @media (min-width: 1020px) {
    .f-basicTitle2 {
      font-size: 18px;
      color: #232323;
      line-height: 26px; } }

.f-basicTitle3 {
  font-weight: 700;
  font-size: 36px;
  color: #232323;
  line-height: 32px; }
  @media (min-width: 1020px) {
    .f-basicTitle3 {
      font-size: 48px;
      color: #232323;
      line-height: 42px; } }

.container {
  min-width: 300px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0 10px; }
  @media (min-width: 700px) {
    .container {
      max-width: 610px; } }
  @media (min-width: 1020px) {
    .container {
      max-width: 930px; } }
  @media (min-width: 1340px) {
    .container {
      max-width: 1250px; } }
